@media (min-width: 992px)
{.modal-lg {
    max-width: fit-content !important;
}}

@media (min-width: 576px)
{.modal-dialog {
    min-width: 30%;
    margin: 0 !important;
}}

.modal{
    margin: 0px;
    display: flex !important;
    justify-content: center;
    align-items: center;
}

